import * as React from 'react'
import { Select, Button, Table, Input, message } from 'antd'
import { inject, observer } from 'mobx-react'
import * as XLSX from 'xlsx'
import './index.scss'
//css-module
// import styles from './index.scss'

const { Option } = Select
interface Props {
    classmanger: any
}

@inject('classmanger')
@observer
class StudentManger extends React.Component<Props> {
    state = {
        columns: [
            {
                title: '',
                dataIndex: 'student_name'
            },
            {
                title: '',
                dataIndex: 'student_id'
            },
            {
                title: '',
                dataIndex: 'grade_name'
            },
            {
                title: '',
                dataIndex: 'room_text'
            },
            {
                title: '',
                dataIndex: 'student_pwd'
            },
            {
                title: '',
                dataIndex: 'index',
                render: (text: any, record: any) => (
                    <a
                        onClick={() => {
                            this.handDelete(record.key)
                        }}>
                        
                    </a>
                )
            }
        ],
        data: [],
        classList: [],
        studentClassList: [],
        value: '',
        classNumber: '',
        studentClassNumber: '',
        student_id: ''
    }
    componentDidMount() {
        this.getList()
    }
    handDelete = (index: any) => {
        console.log(index)
        let { student_id } = this.state.data[index]
        this.setState(
            {
                student_id
            },
            async () => {
                let { studentDelet } = this.props.classmanger
                let result = await studentDelet(this.state.student_id)
                console.log(result)
                if (result.code === 1) {
                    message.success(result.msg)
                    this.getList()
                }
            }
        )
    }
    getList = async () => {
        let {
            getStudentClass,
            getClasses,
            getClassManger
        } = this.props.classmanger
        let result = await getStudentClass()
        let classListData = await getClasses()
        let classMangerList = await getClassManger()
        // console.log(result)
        result.data.map((item: any, index: number) => (item.key = index))
        if (result.code === 1) {
            this.setState({
                data: result.data,
                classList: classListData.data,
                studentClassList: classMangerList.data
            })
        }
    }
    handInput = async (e: any) => {
        let { value } = e.target
        if (value) {
            this.setState({
                value
            })
        } else if (value === '') {
            let { getStudentClass } = this.props.classmanger
            let result = await getStudentClass()
            result.data.map((item: any, index: number) => (item.key = index))
            if (result.code === 1) {
                this.setState({
                    data: result.data,
                    value: ''
                })
            }
        }
    }
    handClick = () => {
        let { value, classNumber, studentClassNumber } = this.state
        if (value) {
            let newData = this.state.data.filter(
                (item: any) => item.student_name === value
            )
            this.setState({
                data: newData
            })
        } else if (classNumber) {
            let newData = this.state.data.filter(
                (item: any) => item.room_text === classNumber
            )
            this.setState({
                data: newData
            })
        } else if (studentClassNumber) {
            let newData = this.state.data.filter(
                (item: any) => item.grade_name === studentClassNumber
            )
            this.setState({
                data: newData
            })
        } else if (value && studentClassNumber) {
            let newData = this.state.data.filter(
                (item: any) =>
                    item.grade_name === studentClassNumber &&
                    item.student_name === value
            )
            this.setState({
                data: newData
            })
        } else if (value && classNumber) {
            let newData = this.state.data.filter(
                (item: any) =>
                    item.room_text === classNumber &&
                    item.student_name === value
            )
            this.setState({
                data: newData
            })
        } else if (classNumber && studentClassNumber) {
            let newData = this.state.data.filter(
                (item: any) =>
                    item.grade_name === studentClassNumber &&
                    item.room_text === classNumber
            )
            this.setState({
                data: newData
            })
        } else if (value && studentClassNumber && classNumber) {
            let newData = this.state.data.filter(
                (item: any) =>
                    item.grade_name === studentClassNumber &&
                    item.room_text === classNumber &&
                    item.student_name === value
            )
            this.setState({
                data: newData
            })
        }
    }
    handSelect = (obj: any) => {
        let { type, value } = obj
        this.setState({
            [type]: value
        })
    }
    handDeleteValue = () => {
        this.setState(
            {
                value: '',
                classNumber: '',
                studentClassNumber: ''
            },
            () => {
                console.log(this.state)
            }
        )
    }
    exportExcel = () => {
        //tableworksheet
        let worksheet = XLSX.utils.json_to_sheet(this.state.data)
        let workbook = XLSX.utils.book_new()
        XLSX.utils.book_append_sheet(workbook, worksheet)

        XLSX.writeFile(workbook, '.xlsx')
    }

    uploadExcel = (e: any) => {
        // console.log(e.target.files)
        let reader = new FileReader()
        reader.onload = function(e: any) {
            // console.log(e.target.result, '.........result')
            var data = new Uint8Array(e.target.result)
            var workbook = XLSX.read(data, { type: 'array' })
            var ws = XLSX.utils.sheet_to_json(workbook.Sheets[0])
            console.log(ws)
        }
        reader.readAsArrayBuffer(e.target.files[0])
    }

    render() {
        let {
            columns,
            data,
            classList,
            studentClassList,
            value,
            classNumber,
            studentClassNumber
        } = this.state
        return (
            <div>
                {/* <h1 className={styles.active}>lalalal</h1> */}
                <h2 className="adduser-title"></h2>
                <div>
                    <div className="student-top">
                        <div className="student-top-item">
                            <Input
                                placeholder=""
                                value={value}
                                onChange={this.handInput}
                            />
                        </div>
                        <div className="student-top-item">
                            <Select
                                placeholder=""
                                defaultValue={classNumber}
                                style={{ width: 160 }}
                                onChange={(value: any) =>
                                    this.handSelect({
                                        value,
                                        type: 'classNumber'
                                    })
                                }>
                                {classList.map((item: any, index: number) => {
                                    return (
                                        <Option
                                            key={index}
                                            value={item.room_text}>
                                            {item.room_text}
                                        </Option>
                                    )
                                })}
                            </Select>
                        </div>
                        <div className="student-top-item">
                            <Select
                                placeholder=""
                                defaultValue={studentClassNumber}
                                style={{ width: 160 }}
                                onChange={(value: any) =>
                                    this.handSelect({
                                        value,
                                        type: 'studentClassNumber'
                                    })
                                }>
                                {studentClassList.map(
                                    (item: any, index: number) => {
                                        return (
                                            <Option
                                                key={index}
                                                value={item.grade_name}>
                                                {item.grade_name}
                                            </Option>
                                        )
                                    }
                                )}
                            </Select>
                        </div>
                        <div className="student-top-item">
                            <Button
                                className="student-btn"
                                onClick={this.handClick}>
                                
                            </Button>
                        </div>
                        <div className="student-top-item">
                            <Button
                                className="student-btn"
                                onClick={this.handDeleteValue}>
                                
                            </Button>
                        </div>
                        <div className="student-top-item">
                            <Button className="student-btn">
                                
                                <input
                                    type="file"
                                    accept=".xlsx"
                                    onChange={this.uploadExcel}
                                />
                            </Button>
                        </div>
                        <div className="student-top-item">
                            <Button
                                className="student-btn"
                                onClick={this.exportExcel}>
                                
                            </Button>
                        </div>
                    </div>
                    <div className="student-bottom">
                        <Table dataSource={data} columns={columns} />
                    </div>
                </div>
            </div>
        )
    }
}
export default StudentManger



